.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.movie-container
{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding-bottom: 30px;
}

.movie-wrapper
{
  width: 500px;
  border-radius: 20px;
  background: white;
  color: #282c34;
  box-shadow: 0px 0px 10px 0px #ddd;
  transition: all .2s;
}

.movie-wrapper:hover
{
  color: #156c85;
  box-shadow: 0px 0px 10px 0px #61dafb;
}

.movie-wrapper img
{
  width: 100%;
}

.movie-search
{
  font-size: 20px;
  font-weight: bold;
  width: 100%;
  height: 100%;
  padding: 0 15px;
  border: none;
  border-radius: 50px;
  transition: all .3s;
}

.movie-search:focus
{
  outline: none;
  box-shadow: 0px 0px 10px 0px #ddd;
  color: #333;
}

.movie-title
{
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 50px;
  font-weight: bold;
  padding: 10px;
}

.movie-date
{
  padding-top: 10px;
  font-style: italic ;
}

.movie-rate
{
  color: tomato;
  font-weight: bold;
  font-size: 1.2em;
  padding: 5px 0 20px 0;
}

.search-wrapper
{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 60px;
  margin-bottom: 40px;
}

@media (max-width: 567px){
  .movie-wrapper
  {
    width: 90%;
  }
  .search-wrapper
  {
    width: 90%;
  }
}